import React, { Component } from 'react';
import Fade from 'react-reveal/Fade'
import './style.css'
import Popup from './Popup';
import { firebase, firebaseDB } from '../../firebase'
import {Redirect} from 'react-router-dom'
import Alert from '../Alert';


class Dashboard extends Component {
    state = {
        user: this.props.user,
        payments: [],
        route: '',
        pop: false,
        redirect: false,
        inv: 0,
        showHistory: false,
        isLoading: false,
        alert: false,
        alertDetails: {
          type: '',
          message: ''
        },
        date: {
            day: 0,
            month: 0,
            date: 0
        }
    }

    componentDidMount(){
        this.getDate()
        setTimeout(() => {
            firebaseDB.ref(`users/${this.state.user.key}`).on('value', e=>{
                console.log(e.val())
    
                    if(e.val().payments){
                        const payments = []
                        const payment = Object.entries(e.val().payments)
                        payment.forEach(e=>{
                            payments.push({...e[1]})
                        })
                        this.setState({payments})
                    }
           })
        }, 1000);

       

    }

    getDate = () => {
        const getDate = new Date()
        const date = {
            day: getDate.getUTCDate(),
            month: getDate.getUTCMonth(),
            year: getDate.getUTCFullYear()
        }
        this.setState({date: {
            day: date.day,
            month: date.month,
            date: `${date.day}/${date.month+1}/${date.year}`
        }})
    }

    
    removePop = () => {
        this.setState({pop: false})
    }

    uploadScreenshot = (e)=>{

        const paymentDate = this.state.date.day
        const dueDate = paymentDate + 7
        
        const months = [31,28,31,30,31,30,31,31,30,31,30,31]

        const month = months[this.state.date.month]

        const due = () =>{
            if(dueDate > month){
                return {
                    day: dueDate - month,
                    month: month + 2
                }
            }else {
                return {
                    day: dueDate,
                    month: this.state.date.month + 1
                }
            }
        }

        let willDue = due()
        console.log(willDue)



        const file = e.target.files[0]
        const name = e.target.files[0].name

        this.setState({
            inv: document.querySelector('.amounttoinv').value,
            isLoading: true
        })
        firebase.storage().ref('Payments').child(`${name}`).put(file).then(e=>{
            console.log(e)
            firebase.storage().ref('Payments').child(`${name}`).getDownloadURL().then((url)=>{
                firebaseDB.ref(`users/${this.state.user.key}/payments`).push().set({
                    screenshot: url,
                    username: this.state.user.username,
                    email: this.state.user.email,
                    amount: this.state.inv,
                    date: this.state.date.date,
                    dueDate: willDue,
                    confirmed: false,
                    isDue: false
                })}).then(e=>{
                    console.log(e, 'succes')

                    this.setState({
                        isLoading: false,
                        alert: true,
                        alertDetails: {
                        type: 'success',
                        message: 'Upload succesful'
                        }
                    })
        
                    this.removePop()
            })
            
        }).catch(e=>{
            console.log(e, 'failed')
            this.setState({
                isLoading: false,
                alert: true,
                alertDetails: {
                  type: 'error',
                  message: e.message
                }
              })
        })
        console.log(file)
    }
    resetPass=()=>{
        firebase.auth().currentUser.updatePassword('newpass')
    }

    toggleHistory = ()=>{
        document.querySelector('.tag').classList.toggle('rotate')
        if (this.state.showHistory) {
            this.setState({showHistory: false})
        } else {
            this.setState({showHistory: true})
        }
        
    }
    render() {
        const inv = 0.0
        const { user } = this.props
        
        return (
            <div className="dashboard">
                {
                this.state.alert?
                <Alert type={this.state.alertDetails.type}
                message={this.state.alertDetails.message} removeAlert={this.removeAlert}/>:null
                }
                
                {
                    this.state.redirect?
                    <Redirect to="/signin"/>:null
                }
                <div className="dash_content">
                    <Fade top>
                        <div className="pic">
                            <img alt="img" src={require('../Home/default.png')}/>
                        </div>
                    </Fade>
                    <Fade bottom delay={1000}>
                        <p className="name">{user.name}</p>
                    </Fade>
                    <Fade left delay={1500}>
                        <p>{user.email}</p>
                        <p>{user.username}</p>
                    </Fade>
                    <div className="earnings">
                        <Fade bottom delay={2000}>
                            <div className="inv">
                                <h1>INVESTED AMOUNT</h1>
                                <p>{this.state.user.totalInvested}</p>
                                <button onClick={this.resetPass}>updatePassword</button>
                            </div>
                        </Fade>
                        <Fade bottom delay={2500}>
                            <div className="earn">
                                <h1>TOTAL EARNRED</h1>
                                <p>0.0</p>
                            </div>
                        </Fade>
                    </div>
                    <Fade bottom duration={1500} delay={3500}>
                        <div style={{textAlign: 'right', marginRight: '2rem'}}>
                            {
                                inv === 0 ?
                                <button onClick={()=>this.setState({pop: true})}>Start investing</button>:
                                <button onClick={()=>this.setState({pop: true})}>Invest more</button>
                            }
                        </div>
                    </Fade>
                    <Fade right>
                        <div className="payment_hostory">
                            <header onClick={this.toggleHistory} className="head">Payment history 
                                    <span className="tag">▲</span>
                            </header>
                            {
                                this.state.payments?
                                this.state.payments.map(e=>{
                                    return <div>
                                        {
                                            this.state.showHistory?
                                            <Fade top>
                                                <div className="payment">
                                                    <p>{e.username}</p>
                                                    <p>{e.date}</p>
                                                    <p>${e.amount}</p>
                                                    {
                                                        e.confirmed?
                                                        'payment confirmed':
                                                        'waiting for confirmation...'
                                                    }
                                                    {
                                                        e.confirmed?
                                                            <div className="recieved_btn">
                                                                {
                                                                    this.state.date.day >= e.dueDate.day && this.state.date.month >= e.dueDate.month?
                                                                    <button>recieved earning</button>:
                                                                    <p>...receiving payment by {e.dueDate.day}/{e.dueDate.month}</p>
                                                                }
                                                            </div>:null
                                                    }
                                                </div>
                                            </Fade>:null
                                        }
                                    </div>
                                }):null
                            }
                        </div>
                    </Fade>
                </div>
                {
                    this.state.pop ?
                    <Popup isLoading={this.state.isLoading} uploadScreenshot={this.uploadScreenshot} removePop={this.removePop}/>:null
                }
            </div>
        );
    }
}

export default Dashboard;