import React, { Component } from 'react';
import './App.css'
import LandingPage from './Components/Home';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Login from './Components/Signin';
import Register from './Components/Register';
import Dashboard from './Components/Dashboard/Dashboard';
import { firebase, firebaseDB } from './firebase'
import Alert from './Components/Alert';

class App extends Component {

  state = {
    users: [],
    user: {},
    redirect: false,
    loading: false,
    alert: false,
    alertDetails: {
      type: '',
      message: ''
    },
    date: {
      day: 0,
      month: 0,
      date: 0
    }
  }
  

  componentDidMount(){
    const users = []
    firebaseDB.ref('users').once('value').then(e=>{
      e.forEach(e=>{
        console.log(e.val())
        users.push({...e.val(), key: e.key})
      })
      console.log(users)
      this.setState({users})

    })
    this.getDate()
    
  }
  getDate = () => {
    const getDate = new Date()
    const date = {
        day: getDate.getUTCDate(),
        month: getDate.getUTCMonth(),
        year: getDate.getUTCFullYear()
    }
    this.setState({date: {
        day: date.day,
        month: date.month,
        date: `${date.day}/${date.month+1}/${date.year}`
    }})
  }

  register = () => {
    this.setState({loading: true})
    const email = document.querySelector('.mail').value
    const password = document.querySelector('.password').value
    firebase.auth().createUserWithEmailAndPassword(email, password).then((e)=>{
        console.log(e, 'registered')
        firebaseDB.ref('users').push({
            name: document.querySelector('.fullname').value,
            username: document.querySelector('.username').value,
            email: document.querySelector('.mail').value,
            totalInvested: 0,
            earnings: 0,
            registeredDate: this.state.date.date,
            payments: []
        }).then(e=>{
            console.log(e.key)
            this.setState({
                loading: false,
                user: {
                  name: document.querySelector('.fullname').value,
                  username: document.querySelector('.username').value,
                  email: document.querySelector('.mail').value,
                  totalInvested: 0,
                  earnings: 0,
                  key: e.key,
                  registeredDate: this.state.date.date,
                  payments: ['Here is your payment history']
                },
                redirect: true,
              })
        })
    }).catch((e)=>{
        console.log(e, 'false')
        this.setState({
            loading: false,
            redirect: false,
            alert: true,
            alertDetails: {
              type: 'error',
              message: e.message
            }
          })
    })
  }
  authUser = () => {
    this.setState({loading: true})
    const email = document.querySelector('.email').value
    const password = document.querySelector('.pass').value
    firebase.auth().signInWithEmailAndPassword(email, password).then((e)=>{
        console.log('success')
        this.state.users.forEach(e=>{
          if(e.email === email) {
            this.setState({
              loading: false,
              redirect: true,
              user: e
            })
          }else {
            console.log(e.email, email)
          }
        })
        
    }).catch((e)=>{
        console.log('no way')
        this.setState({
            loading: false,
            redirect: false,
            alert: true,
            alertDetails: {
              type: 'error',
              message: e.message
            }
          })
    })
  }


  removeAlert = () => {
    this.setState({alert: false})
  }

  render() {
    return (
      <Router>
          <div>
            {
              this.state.alert?
              <Alert type={this.state.alertDetails.type}
               message={this.state.alertDetails.message} removeAlert={this.removeAlert}/>:null
            }
            <Switch>
              <Route path="/me">
                {
                    this.state.user.email?
                      <Dashboard user={this.state.user}/>
                    :<Redirect to="signin"/>
                }
              </Route>
              <Route path="/signin">
                  <Login authUser={this.authUser} loading={this.state.loading} 
                  redirect={this.state.redirect}/>
              </Route>
              <Route path="/register">
                  <Register register={this.register} loading={this.state.loading} 
                  redirect={this.state.redirect} />
              </Route>
              <Route path="/">
                <LandingPage name={'landing'}/>
              </Route>
            </Switch>
          </div>
      </Router>
    );
  }
}

export default App;